<!-- ,currentPageGoodsInfogoodsInfo
80-70-7currentPageGoodsInfo 8-15-->
      <div class="divide_page_div">
    <el-pagination
    @current-change="handleCurrentChange"
    :current-page="currentPage"
    :page-size="pageSize"
    background
    layout="prev, pager, next"
    :total="goodsInfo.length">
  </el-pagination>
