</template>

<script>

export default {
   data() {
    return {
      currentPage:1, //
      pageSize:8,    //
      currentPageGoodsInfo: [],// 
      goodsInfo:[],//
     
        
    };
  },
    beforeCreate() {
    // console.log('product-List beforeCreate ');
  },
   created() {
    //  console.log('product-List reated ');
      this.$http.get(this.apis.find_all_goods).then(res=>{
      console.log(res)
      if (res.data.code===200){
        this.goodsInfo=res.data.data;//goodsInfo 
        //  goodsImgUrlgoodsDescriptiongoodsPrice 
        // console.log("goodsInfo")
        // console.log(this.goodsInfo);
        console.log(this.goodsInfo.length);
        //
        for(let i=0;i<this.goodsInfo.length;i++){
          if( this.goodsInfo[i].goodsDescription.length>10){
          this.goodsInfo[i].goodsDescription=this.goodsInfo[i].goodsDescription.slice(0,10)+'...';//

          }
        }

    // console.log('hhhhhhhh');
    // 8goodsInfo8 currentPageGoodsInfo
    if(this.goodsInfo.length>8){
    this.currentPageGoodsInfo = this.goodsInfo.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize);
    }else{
      //  console.log('kkkkkk');
       //8currentPageGoodsInfo
      this.currentPageGoodsInfo = this.goodsInfo;
      console.log( this.currentPageGoodsInfo);
    }
      }
    }).catch(error=>{
      console.log(error)
    })

   },
    beforeMount() {
    // console.log('product-List  beforeMount ');
    
  },
  mounted() {
    // console.log('product-List  mounted ');
    
  },
  methods: {
    // query 
    goGoodsDetail(id){
        this.$router.push({path:"/goodsDetail",query:{goodsId:id}});
    },
    // currentPage 
      handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                console.log('',this.currentPage)  //
    this.currentPageGoodsInfo = this.goodsInfo.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize);
              
        },
  }
}
</script>

