<template>
  <div id="release_goods">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="ruleForm"
    >
      <!--  -->
      <div class="goods-text">
      <el-form-item label="" prop="goodsDescription" >
        <el-input
          type="textarea"
          v-model="ruleForm.goodsDescription"
          :autosize="{ minRows: 2, maxRows: 4 }"
        ></el-input>
      </el-form-item>
</div>
      <!--   prop -->
      <el-form-item label="" prop="goodsType" class="goods-select">
        <el-select v-model="ruleForm.goodsType" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <!--  -->
        <!-- oninput="value=value.replace(/^\.+|[^\d.]/g,'')" -->
         <!--  oninput="value=value.replace(/[^\d]/g, '')" -->
      <el-form-item label="" prop="goodsPrice" class="goods_price_item">
        <!-- v-model.number  String number data -->
          <!--  --> 
        <el-input
          v-model="ruleForm.goodsPrice"
            oninput="value=value.replace(/[^\d.]/g,'')"
        ></el-input>
      </el-form-item>

      <!--   prop rules -->
      <el-form-item
        label=""
        prop="phone"
        class="contact_item"
      >
        <el-input v-model="ruleForm.phone" ></el-input>
      </el-form-item>

          <!-- accept="image/png,image/gif,image/jpg,image/jpeg" -->
      <!--  -->
      <el-form-item label="" prop="goodsImgUrl">
          <el-upload
               class="upload-demo"
               :action="uploadFileHost"
               :on-remove="handleAvatarRemove"
               :on-success="handleAvatarSuccess"
               :before-upload="handleBeforeUpload"
               :file-list="image_avatar"
               :data="uploadFileData"
           
               list-type="picture"
               :limit="1"
               style="width: 300px">
               <el-button size="small" type="primary"></el-button>
               <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
             </el-upload>

             <!-- <el-dialog :visible.sync="dialogVisible">
  <img width="100%" :src="dialogImageUrl" alt="">
</el-dialog> -->
      </el-form-item>

      <!--  -->
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button type="info" @click="resetForm('ruleForm')"></el-button>
      </el-form-item>

    </el-form>
  </div>
</template>

<script>
export default {
  name: "rg",
  data() {
    return {
      ruleForm: {//
        goodsDescription: "",
        goodsType: "",
        goodsPrice: '',
        phone: "",
        goodsImgUrl:'',//url
        userId:0,
      },
      
       uploadFileHost:'',//
      image_avatar:[],//
      uploadFileData:{//
        'key':'',//
        'OSSAccessKeyId':'',
        'policy':'',
        'signature':'',
        'success_action_status': '200'
      },
      tempImageUrl:'',

      rules: {
        goodsType: [
          //    message
          { required: true, message: "", trigger: "change" },
        ],
        goodsDescription: [
          { required: true, message: "", trigger: "blur" },
        ],
        goodsPrice: [
          { required: true, message: "", trigger: "blur" },
        ],
        //    trigger: 'blur'
        contactWays: [
          { required: true, message: "", trigger: "blur" },
        ],
        //  goodsImgUrl 
          goodsImgUrl: [
          { required: true, message: "", trigger: "change" },
        ],
      
      },
    };
  },
  created() {
    console.log(" created ");
    // userId int sessionStorage  string number userId
    this.ruleForm.userId=Number(sessionStorage.getItem("userId"));
  },
  methods: {
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert('submit!');
          //  this  vue 
          // alert(this.ruleForm.goodsPrice);
           console.log("");
          //   price doublenumber
        this.ruleForm.goodsPrice= Number(this.ruleForm.goodsPrice);
        console.log(this.ruleForm);
        // http url
         this.$http.post(this.apis.add_goods,this.ruleForm).then(res=>{
              console.log(res)
              if (res.data.code===200){
                this.$message({
                  message: res.data.msg,
                  type: 'success'
                });
                this.resetForm('ruleForm');
              }else {
                this.$message.error(res.data.msg);
              }
            }).catch(error=>{
              this.$message.error("");
            });

        } else { //
          console.log("error submit!!");
          return false;
        }

      });
    },

    // 
    // ref,js$refs
    // resetFields()
    resetForm(formName) {
      // 
      this.$refs[formName].resetFields();
        this.image_avatar=[];
        this.ruleForm.goodsImgUrl='';
    },

//  beforeUpload   file, file.type, before-upload 
// auto-uploadfalsebefore-uploadon-changebefore-upload
    // 

    // urlurl 
    //url
      handleBeforeUpload(file){
        const imageType = file.type === 'image/jpeg'||file.type==='image/png';
        const isLt2M = file.size / 1024 / 1024 < 10;

        if (!imageType) {
          this.$message.error(' JPG,PNG!');
          return false;//
        }
        if (!isLt2M) {
          this.$message.error(' 10MB!');
          return false;
        }
        console.log(file)
        console.log("");
        // url
        return new Promise((resolve,reject)=>{
          this.$http.get(this.apis.oss_policy).then(res=>{
            if(res.data.code==200){
                console.log("");
              console.log(res.data.data)
              //
              let suffixName=file.name.substring(file.name.lastIndexOf('.') + 1);//
              let nowDate = new Date();
              let fileKey=res.data.data.dir+'/'+nowDate.getTime()+'/'+Math.random()*10000+'.'+suffixName//
              this.uploadFileHost=res.data.data.host
              this.uploadFileData.key=fileKey
              this.uploadFileData.OSSAccessKeyId=res.data.data.ossAccessKeyId
              this.uploadFileData.policy=res.data.data.policy
              this.uploadFileData.signature=res.data.data.signature
              this.tempImageUrl=res.data.data.host+'/'+fileKey;
              console.log("url");
              //urlfd 
              let fd=new FormData()
              fd.append("file",file);
              fd.append("key",fileKey);
              fd.append("OSSAccessKeyId",res.data.data.ossAccessKeyId)
              fd.append("policy",res.data.data.policy)
              fd.append("signature",res.data.data.signature)
                console.log("");
              resolve("")
            }else {
                console.log("");
              reject("")
            }
          }).catch(error=>{
            console.log("catch");
            reject("")
          })
        }
        )

      },

// 
  handleAvatarRemove(file, fileList) {
        this.image_avatar=[];
        // console.log(file, fileList);
        console.log("")
        this.ruleForm.goodsImgUrl='';
      },

  // 
      handleAvatarSuccess(response, file, fileList){
        console.log("")
        console.log(file);
        this.ruleForm.goodsImgUrl=this.tempImageUrl;//url
        console.log("url"+this.ruleForm.goodsImgUrl);
      },

  },
  
};
</script>

<style scoped src='../../style/release-goods.css'>
/* ../  ../ */

</style>
