<template>
  <div>
    <el-table :data="tableData" stripe style="width: 100%" height="640">
      <!-- width -->
      <!-- propdata-->
      <!--   el-table-columnprop-->
      <el-table-column prop="goodsDescription" label="" width="180"> </el-table-column>
      <!--  -->
      <!--,scope.row,img,img,-->
      <el-table-column prop="" label="" width="180">
        <!-- templateimg -->
        <template slot-scope="scope">
          <div class="goods_img_div">
          <img :src="scope.row.goodsImgUrl" alt="" >
          </div>
        </template>
      </el-table-column>
      <!--  -->
      <el-table-column prop="goodsPrice" label=""> </el-table-column>
      <!--  -->
        <el-table-column label="">
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      //  tableData
      tableData: [],
    };
  },

  created() {
    console.log(" created");
    this.getUserAllGoods();
   },

    methods: {
      //
      //data
      //
      handleDelete(index, row) {
        // alert(row.goodsId);// row row.goodsId 
   this.$http.delete(this.apis.user_delete_goods+"?goodsId="+row.goodsId).then(res=>{
      console.log(res)
      if (res.data.code===200){// 200 
     console.log("");
         this.$message({
                  // message: res.data.msg,
                  message:'',
                  type: 'success'
                });
    this.getUserAllGoods();//

      }
    }).catch(error=>{
      console.log(error)
    })
      },

      //
      getUserAllGoods(){
 let userId=Number(sessionStorage.getItem("userId"));//sessionStor userIduserId
     console.log(userId);
      this.$http.get(this.apis.find_user_goods+"?userId="+userId).then(res=>{
      console.log(res)
      if (res.data.code===200){
     console.log("tableData ");
        this.tableData=res.data.data;

      }
    }).catch(error=>{
      console.log(error)
    })

      },
    }


};

</script>

<style scoped>
.goods_img_div{
  width: 80px;
  height: 60px;
}

.goods_img_div img{
  width: 100%;
  height: 100%;
}
