<template>
  <div id="lost_report">
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm"
    >
      <!--  -->
      <el-form-item label="" prop="lostThing">
        <el-input
          type="textarea"
          v-model="ruleForm.lostThing"
          :autosize="{ minRows: 2, maxRows: 4 }"
        ></el-input>
      </el-form-item>

      <!--  -->
      <el-form-item
        label=""
        prop="phone"
        class="contact_item"
      >
        <el-input v-model="ruleForm.phone"></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item label="" prop="imgUrl">
          <el-upload
               class="upload-demo"
               :action="uploadFileHost"
               :on-remove="handleAvatarRemove"
               :on-success="handleAvatarSuccess"
               :before-upload="handleBeforeUpload"
               :file-list="image_avatar"
               :data="uploadFileData"
           
               list-type="picture"
               :limit="1"
               style="width: 300px">
               <el-button size="small" type="primary"></el-button>
               <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
             </el-upload>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        <el-button type="info" @click="resetForm('ruleForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "rg",
  data() {
    return {
      ruleForm: {
        lostThing: "",
        phone: "",
        imgUrl:'',//url
        userId:0,
      },
      
       uploadFileHost:'',
      image_avatar:[],
      uploadFileData:{
        'key':'',//
        'OSSAccessKeyId':'',
        'policy':'',
        'signature':'',
        'success_action_status': '200'
      },
      tempImageUrl:'',

      rules: {
       
        lostThing: [
          { required: true, message: "", trigger: "blur" },
        ],
    
        //    trigger: 'blur'
        phone: [
          { required: true, message: "", trigger: "blur" },
        ],
        //  goodsImgUrl 
          imgUrl: [
          { required: true, message: "", trigger: "change" },
        ],
      
      },
    };
  },
  created() {
    console.log(" created ");
    // userId int sessionStorage  string number  userId
    this.ruleForm.userId=Number(sessionStorage.getItem("userId"));
  },
  methods: {
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
           console.log("");
        console.log(this.ruleForm);
        // http url
         this.$http.post(this.apis.report_lost,this.ruleForm).then(res=>{
              console.log(res)
              if (res.data.code===200){
                this.$message({
                  message: res.data.msg,
                  type: 'success'
                });
                this.resetForm('ruleForm');
              }else {
                this.$message.error(res.data.msg);
              }
            }).catch(error=>{
              this.$message.error("");
            });

        } else { //
          console.log("error submit!!");
          return false;
        }

      });
    },

    // 
    // ref,js$refs
    // resetFields()
    resetForm(formName) {
      // 
      this.$refs[formName].resetFields();
        this.image_avatar=[];
        this.ruleForm.imgUrl='';
    },

//  beforeUpload   file, file.type, before-upload 
// auto-uploadfalsebefore-uploadon-changebefore-upload
    // 
    // urlurl 
      handleBeforeUpload(file){
        const imageType = file.type === 'image/jpeg'||file.type==='image/png';
        const isLt2M = file.size / 1024 / 1024 < 10;

        if (!imageType) {
          this.$message.error(' JPG,PNG!');
          return false;
        }
        if (!isLt2M) {
          this.$message.error(' 10MB!');
          return false;
        }
        console.log(file)
        console.log("");
        // url
        return new Promise((resolve,reject)=>{
          this.$http.get(this.apis.oss_policy).then(res=>{
            if(res.data.code==200){
                console.log("");
              console.log(res.data.data)
              //
              let suffixName=file.name.substring(file.name.lastIndexOf('.') + 1);//
              let nowDate = new Date();
              let fileKey=res.data.data.dir+'/'+nowDate.getTime()+'/'+Math.random()*10000+'.'+suffixName//
              this.uploadFileHost=res.data.data.host
              this.uploadFileData.key=fileKey
              this.uploadFileData.OSSAccessKeyId=res.data.data.ossAccessKeyId
              this.uploadFileData.policy=res.data.data.policy
              this.uploadFileData.signature=res.data.data.signature
              this.tempImageUrl=res.data.data.host+'/'+fileKey;
              console.log("url");
              let fd=new FormData()
              fd.append("file",file);
              fd.append("key",fileKey);
              fd.append("OSSAccessKeyId",res.data.data.ossAccessKeyId)
              fd.append("policy",res.data.data.policy)
              fd.append("signature",res.data.data.signature)
                console.log("");
              resolve("")
            }else {
                console.log("");
              reject("")
            }
          }).catch(error=>{
            console.log("catch");
            reject("")
          })
        }
        )

      },

// 
  handleAvatarRemove(file, fileList) {
        this.image_avatar=[];
        // console.log(file, fileList);
        console.log("avatar ")
        this.ruleForm.imgUrl='';
      },

  // 
      handleAvatarSuccess(response, file, fileList){
        console.log("")
        console.log(file);
        this.ruleForm.imgUrl=this.tempImageUrl;// url
       
      },

  },
  
};
</script>

<style scoped>
.demo-ruleForm {
  margin-top: 30px;
  margin-right: 30px;
}

@media only screen and (min-width: 768px) {
  .contact_item>>>.el-input__inner{
    width: 50%;
    border: 1px solid orange;
  }
}
/* elementUI width */

</style>
