import Vue from 'vue'
import App from './App.vue'
// 
import router from './router'
// 
import 'font-awesome/css/font-awesome.min.css';
import Axios from 'axios'
// Vue.prototype.$axios = Axios 
//:this.$axios
Vue.prototype.$http = Axios //Axiosvue$http vue.vueaaxios

import { apis } from './config'
Vue.prototype.apis = apis //apivueVue newapi

Vue.config.productionTip = false


//token
// 
//token
Axios.interceptors.request.use(
    config => {
        /*8080/#/*/
        //url
        //tokentokentokentoken
        if (sessionStorage.getItem("token")) { // tokentoken if 

            // `baseURL`  `url`  `url`  URL
            // config.baseURL = 'http://47.106.64.140:8007'
            let token = sessionStorage.getItem("token")
            let userId = sessionStorage.getItem("userId")
            console.log("****************token********************")
            console.log(token);
            console.log("****************userId********************")
            console.log(userId);
            console.log("************************************")
                // tokentoken 
            if (token) {
                config.headers.token = token
            }
            if (userId) {
                config.headers.userId = userId;
            }
        }
        return config; //
    },
    error => {
        return Promise.reject(error);
    }
)


//token
Axios.interceptors.response.use(
    response => { //
        console.log("");
        return response;
    },
    error => {
        if (error.response) {
            switch (error.response.status) {
                // status
                case 401: //token token  401 token
                    // window.sessionStorage.clear();
                    sessionStorage.removeItem('token');
                    this.$router.push({ path: '/login' })
                    break;
            }
        }
        return Promise.reject(error.response.data);
    }
)



new Vue({
    // Vueroutervue.vue vue this.$router
    router,
    render: h => h(App), //App.vuenew vueindex.html div 
}).$mount('#app')